@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff ttf svg) {
  $src: null;

  $extmods: (
          eot: "?",
          svg: "#" + str-replace($name, " ", "_")
  );

  $formats: (
          otf: "opentype",
          ttf: "truetype"
  );

  @each $ext in $exts {
    $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
    $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
    $src: append($src, url(quote($path + "." + $extmod)) format(quote($format)), comma);
  }

  @font-face {
    font-family: quote($name);
    font-style: $style;
    font-weight: $weight;
    src: $src;
  }
}
@include font-face(PTSansPro-Bold, "../fonts/PTSansPro-Bold", bold, sans-serif, eot woff ttf svg);
@include font-face(PTSansPro-Demi, "../fonts/PTSansPro-Demi", normal, sans-serif, eot woff ttf svg);
@include font-face(PTSansPro-Regular, "../fonts/PTSansPro-Regular", normal, sans-serif, eot woff ttf svg);
